@import url(https://fonts.googleapis.com/css2?family=Finlandica:ital,wght@0,400;0,500;0,700;1,400&display=swap);
*:where(:not(html,iframe,canvas,img,svg,video):not(svg *,symbol *)){all:unset;display:revert}*,*::before,*::after{box-sizing:border-box}a,button{cursor:revert}ol,ul,menu{list-style:none}img{max-width:100%}table{border-collapse:collapse}input,textarea{-webkit-user-select:auto}textarea{white-space:revert}meter{-webkit-appearance:revert;appearance:revert}::-webkit-input-placeholder{color:unset}:-ms-input-placeholder{color:unset}::placeholder{color:unset}:where([hidden]){display:none}:where([contenteditable]:not([contenteditable="false"])){-moz-user-modify:read-write;-webkit-user-modify:read-write;overflow-wrap:break-word;-webkit-line-break:after-white-space;-webkit-user-select:auto}:where([draggable="true"]){-webkit-user-drag:element}

body{font-size:16px;font-family:"Finlandica", Arial, sans-serif}.app{color:#282a2d;background-color:#e5e9f1}.app__title{font-size:32px;font-weight:bold;text-align:center}

.todo-list{display:grid;grid-template-columns:repeat(auto-fill, 500px);justify-content:center;grid-gap:22px;gap:22px;padding:10px}.todo-list__item{display:flex;flex-direction:column;grid-gap:10px;gap:10px;padding:30px;border-radius:20px;background-color:#fefefe;transition:box-shadow 200ms, -webkit-transform 200ms;transition:transform 200ms, box-shadow 200ms;transition:transform 200ms, box-shadow 200ms, -webkit-transform 200ms}.todo-list__item:hover{-webkit-transform:scale(1.05);transform:scale(1.05);box-shadow:0 0 20px 10px rgba(0,0,0,0.05)}.todo-list__divider{height:1px;background:#dcdcdc}@media (min-width: 1600px){.todo-list{grid-template-columns:repeat(3, 500px)}}

.todo-info{display:flex;justify-content:space-between;grid-gap:10px;gap:10px;font-weight:500}

.user-info{display:flex;align-items:center;grid-gap:10px;gap:10px;font-size:14px}.user-info__email{margin-left:auto}

